<script setup>

import {store} from "@/store";
import {onMounted, ref} from "vue";
import requestService from "@/utils/request";
import router from "@/router";

const loadingFollow = ref(false)

onMounted(() => {
  getFriendList();
})

const friendList = ref([])

const getFriendList = () => {
  requestService.get("/friend/list/"+store.userInfo.userId).then((res) => {
    if (res.data.code === 2001) {
      friendList.value = res.data.data
      friendListCount.value = friendList.value.length
    }
  })
}

const chat = (userId) => {
  router.push({ path: '/chat/chatWith', query: { uid: userId } });
}

const toUserCenter = (userId) => {
  router.push({path: '/userCenter/otherUserCenter', query: {userId: userId}})
}

const friendListCount = ref(0);

</script>

<template>
  <div :style="{width:'600px',height:'fit-content'}">
    <div :style="{margin:'5px 0',letterSpacing:'1px'}">全部好友({{ friendListCount }})</div>

    <div>
      <a-card :loading="loadingFollow" class="follow-list" v-for="(item,index) in friendList" :key="index"
      >
        <a-card-meta>
          <template #title>
            <div class="nickname" @click="toUserCenter(item.userId)" style="cursor: pointer" >{{ item.nickName }}</div>
          </template>
          <template #avatar>
            <a-avatar class="avatar" shape="circle" :src="item.avatar" :size="60"
                      @click="toUserCenter(item.userId)"
                      style="cursor: pointer"
            />
          </template>
          <template #description>
            <a-typography-text class="desc" :style="{fontSize:'13px',color:' #c2c2c2'}"
                               :content="item.description"/>
          </template>
        </a-card-meta>

        <a-button @click="chat(item.userId)" class="follow-btn" shape="round">发消息</a-button>
      </a-card>


    </div>


  </div>

</template>

<style scoped>
.avatar {

}

.nickname {
  margin-top: 5px;
}

.desc {
  margin-top: 8px;
}

.follow-btn {
  width: 90px;
  position: absolute;
  box-shadow: none;
  margin-top: -40px;
  margin-left: 460px;
}

.follow-btn:hover {
  box-shadow: none;
  color: #de1c31;
  border: 1px #de1c31 solid;
}

.follow-list {
  margin-bottom: 10px;
  width: 630px;
  display: flex;
  padding: 18px 15px;
}

.follow-list:hover {

}
</style>